ul {
    list-style-type: none;
}

canvas {
    display: block;
    position: relative;
    width: 100%;
    height: 250px;
    z-index: 1;
}

.App {
    width: 100%;
}
.colCanvas {
    position: relative;
    margin-top: -250px;
    z-index: 0;
}

.fret {
    display: block;
    position: absolute;
    padding: 0;
    margin: 0;
}

.strings {
    width: 100%;
    position: relative;
    z-index: 2;
    display: flex;
    padding: 0;
    margin: 0;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: stretch;
}

.string {
    display: flex;
    padding: 0;
    margin: 0;
    flex-flow: row nowrap;
    justify-content: space-between;
    margin: 0;
}

button {
    height: 100%;
    width: 100%;
    opacity: 0;
}

button:hover {
    opacity: 0.5;
}